import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/komen'

const API_KEY = config["apiKey"];

export function* tambahKomen(action) {

  const {komen, penulis, idposting, tanggal} = action.edit;

  const localId = localStorage.getItem("localId");
  try{

    const urlDB = `https://repository-1d687.firebaseio.com/komen.json`

    yield axios.post(urlDB, {komen, penulis, idposting, tanggal })


    // nik: nik => nik

  } catch (error){
    console.log(error);
  }

}

export function* lihatKomen(action){
  const urlDBKomen         =`https://repository-1d687.firebaseio.com/komen.json?orderBy="idposting"&equalTo="${action.id}"`;
    const urlDBUser       = 'https://repository-1d687.firebaseio.com/user.json';

try{
  const resDBKomen      = yield axios.get(urlDBKomen);
  const resDBUser       = yield axios.get(urlDBUser);

  for(let key in resDBKomen.data){
    resDBKomen.data[key].id_penulis = resDBKomen.data[key].penulis;
    resDBKomen.data[key].penulis = resDBUser.data[resDBKomen.data[key].penulis].name;
  }

  console.log('saga', resDBKomen);

  yield put(actions.lihatKomenSukses(resDBKomen.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

  }catch (error){
    console.log(error);

  }

}
