export const register = (data) => {

  return{
    type: "MEMBER_REGISTRASI",
    data,
  };
}

export const registerSukses = (idToken, localId,name, email) => {

  return{
    type: "MEMBER_REGISTRASI_SUKSES",
    idToken, localId, name, email
  };
}

export const login = (email, password) =>{
  return{
    type: "MEMBER_LOGIN",
    email, password,
  };
}

export const loginSukses = (idToken, localId, email, nik, alamat, bidang, jenjang, jurusan, name, universitas) => {
  return{
    type: "MEMBER_LOGIN_SUKSES",
    idToken, localId, email, nik, alamat, bidang, jenjang, jurusan, name,  universitas,
  }
}

export const authCheckState = () => {
  return {
    type: "MEMBER_CHECK_STATE",
  };
};

export const getUserData = (idToken, localId) => {
  return {
    type: "MEMBER_GET_DATA",
    idToken, localId,
  };
};

export const logout = () => {
  return{
    type: "MEMBER_LOGOUT"
  }
}

export const updateProfile = (edit) => {
  return{
    type: "MEMBER_UPDATE_PROFILE",
    edit,
  }
}

export const tambahPosting = (edit) => {
  return{
    type: "MEMBER_TAMBAH_POSTING",
    edit,
  }
}
