import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import DateFnsUtils from '@date-io/date-fns';
import Grid from '@material-ui/core/Grid';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { makeStyles } from '@material-ui/core/styles';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/hki';
import { useHistory } from 'react-router';

export default function TambahHKI() {

		return(
			<div className="animated fadeIn">
        <Header />
        <Data />
			</div>
		);
}

function Header(){
  return(
    <div className="App judul">
      <h1> Tambah HKI </h1>
      <p> Bagikan HKI anda</p>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

function Data() {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();

  const [edit, setEdit] = useState({
    judul: "",
    id: "",
    kategori: "",
    tahun: "",
  })

  //const [value, setValue] = React.useState('penelitian');

  const handleChange = (event) => {
    //setEdit(event.target.value);

    setEdit({...edit, tipe: event.target.value})
  };

  const date = new Date()
  const [selectedDate, setSelectedDate] = React.useState(date);

  const handleDateChange = (date) => {
    setSelectedDate(date);
    setEdit({...edit, tanggal: selectedDate });

  };

  const inputChangeHandler = (type) => (event) => {
    setEdit({...edit, [type]: event.target.value});
  };

  function tambahHKI(event){
    event.preventDefault();
    dispatch(actions.tambahHKI(edit));
    history.push("/profile");
  }
	return(
		<Row>
		<Col>
		<Card>
			<CardBody>
				<Form  className="form-horizontal">
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Judul</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder={edit.judul} value={edit.judul} onChange={inputChangeHandler('judul')} />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">ID</Label>
            <Col sm="6">
              <Input type="text" id="input-normal" name="input-small" placeholder={edit.id} value={edit.id} onChange={inputChangeHandler('id')} />
            </Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal"> Kategori </Label>
            <Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder={edit.kategori} value={edit.kategori} onChange={inputChangeHandler('kategori')} />
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">Tahun Permohonan</Label>
            <Col sm="6">
              <Input type="text" id="input-normal" name="input-small" placeholder={edit.tahun} value={edit.tahun} onChange={inputChangeHandler('tahun')} />
            </Col>
					</FormGroup>
            <Button variant="contained" color="primary" onClick={tambahHKI}>
            Submit
          </Button>

				</Form>
			</CardBody>
		</Card>
		</Col>
		</Row>

	);
}
