import React, { useEffect } from 'react';
import './App.css';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import axios from 'axios';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {config} from './config/firebase';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/posting';

const API_KEY = config["apiKey"];
const databaseURL = config['databaseURL']



export default function Home() {
  const dispatch = useDispatch();
  const {daftarPengabdian, daftarPenelitian} = useSelector(state => state.posting);
    useEffect( ()=> {
      dispatch(actions.lihatPosting());
    },[] )


    if(!daftarPengabdian || !daftarPenelitian) {
      return null;
    }

		return (
      <div className="animated fadeIn">
        {Object.keys(daftarPengabdian).map(key => (
          <Pengabdian
              name={daftarPengabdian[key].penulis}
              judul={daftarPengabdian[key].judul}
              tanggal={moment(daftarPengabdian[key].tanggal).format('LL')}
              id={key}
            />
          ))}

          {Object.keys(daftarPenelitian).map(key => (
            <Penelitian
                name={daftarPenelitian[key].penulis}
                judul={daftarPenelitian[key].judul}
                tanggal={moment(daftarPenelitian[key].tanggal).format('LL')}
                id={key}
              />
            ))}
      <br/>
      <br/>
      </div>
	  )
}
