export const tambahHKI = (edit) => {

  return{
    type: "HKI_TAMBAH_HKI",
    edit,
  }
}

export const lihatHKI = () => {
  return{
    type: "HKI_LIHAT_HKI",
  }
}

export const lihatHKIall = (id) => {
  return{
    type: "HKI_LIHAT_HKIALL",
    id,
  }
}

export const lihatHKISukses = (daftarHKI) => {

  return{
    type: "HKI_LIHAT_HKI_SUKSES", //buat ke reducer
    daftarHKI,
  }
}

export const lihatHKIallSukses = (daftarHKIall) => {

  return{
    type: "HKI_LIHAT_HKIALL_SUKSES", //buat ke reducer
    daftarHKIall,
  }
}

export const editHKI = (edit) => {

  return{
    type: "HKI_EDIT_HKI",
    edit,
  }
}

export const hapusHKI = (idHKI) =>{
  return{
    type: "HKI_HAPUS_HKI",
    idHKI,
  }
}
