export const tambahBuku = (edit) => {

  return{
    type: "BUKU_TAMBAH_BUKU",
    edit,
  }
}

export const lihatBuku = () => {
  return{
    type: "BUKU_LIHAT_BUKU",
  }
}

export const lihatBukuall = (id) => {
  return{
    type: "BUKU_LIHAT_BUKUALL",
    id,
  }
}

export const lihatBukuSukses = (daftarBuku) => {
  return{
    type: "BUKU_LIHAT_BUKU_SUKSES", //buat ke reducer
    daftarBuku,
  }
}

export const lihatBukuallSukses = (daftarBukuall) => {
  return{
    type: "BUKU_LIHAT_BUKUALL_SUKSES", //buat ke reducer
    daftarBukuall,
  }
}

export const editBuku = (edit) => {

  return{
    type: "BUKU_EDIT_BUKU",
    edit,
  }
}

export const hapusBuku = (idBuku) =>{
  return{
    type: "BUKU_HAPUS_BUKU",
    idBuku,
  }
}
