export const tambahKomen = (edit) => {
  return{
    type: "KOMEN_TAMBAH_KOMEN",
    edit,
  }
}

export const lihatKomen = (id) => {
  return{
    type: "KOMEN_LIHAT_KOMEN",
    id,
  }
}

export const lihatKomenSukses = (daftarKomen) => {

  return{
    type: "KOMEN_LIHAT_KOMEN_SUKSES", //buat ke reducer
    daftarKomen,
  }
}
