import React, {Component} from 'react';
import '../App.css';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';

export default function Pengabdian(props){
  return(
    <Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Pengabdian by {props.name}</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					<a className="App-judul">{props.judul}</a>
					<br/>
					<a> {props.tanggal}</a>
					</CardFooter>
				</Card>
        <Link to={"/postpengabdian/"+props.id}>
          <Button>
            <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a>
          </Button>
        </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
			</Col>
		</Row>
  );
}
