import { updateState } from "../utils/updateState";

const initialState = {
  daftarBuku: null,
  daftarBukuall: null,

};

const lihatBukuSukses = (state, action) =>{
  return updateState(state, {daftarBuku: action.daftarBuku }  )
}

const lihatBukuallSukses = (state, action) =>{
  return updateState(state, {daftarBukuall: action.daftarBukuall }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "BUKU_LIHAT_BUKU_SUKSES":
    return lihatBukuSukses(state, action);

    case "BUKU_LIHAT_BUKUALL_SUKSES":
    return lihatBukuallSukses(state, action);

    default: return state;

  }

};




export default reducer;
