import { updateState } from "../utils/updateState";

const initialState = {
  daftarPenelitian: null,
  daftarPengabdian: null,

};

const lihatPostingSukses = (state, action) =>{
  return updateState(state, {daftarPenelitian: action.daftarPenelitian, daftarPengabdian: action.daftarPengabdian }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "POSTING_LIHAT_POSTING_SUKSES":
    return lihatPostingSukses(state, action);



    default: return state;

  }

};




export default reducer;
