import 'date-fns';
import React, { useState, useEffect } from 'react';
import './App.css';
import header from './header.JPG';
import logo from './Peteng2.png';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import { makeStyles } from '@material-ui/core/styles';
import DateFnsUtils from '@date-io/date-fns';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import moment from 'moment';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/posting';
import { useHistory } from 'react-router';
import { useParams } from 'react-router';

export default function EditPosting() {
		return(

			<div className="animated fadeIn">
        <Header />
        <Data />
			</div>
		);
}


function Header(){
  return(
    <div className="App judul">
      <h1> Edit Postingan </h1>
      <p> Benahi postingan anda</p>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

function Data() {
  const classes = useStyles();
  const history = useHistory();
  const location = useParams();
  const dispatch = useDispatch();
  const {daftarPengabdian, daftarPenelitian} = useSelector(state => state.posting);

  const [edit, setEdit] = useState({
    judul: "",
    tanggal: "",
    abstrak: "",
    file: "",
    tipe: "penelitian"
  })


  useEffect( ()=> {
    if (daftarPenelitian[location.id]){
      setEdit(daftarPenelitian[location.id]);
    } else if (daftarPengabdian[location.id]){
      setEdit(daftarPengabdian[location.id]);
    }else {
      dispatch(actions.lihatPosting());
    }
  },[daftarPenelitian,daftarPengabdian,  location]   )


console.log('daftarPengabdian',daftarPengabdian[location.id]);


  //const [value, setValue] = React.useState('penelitian');

  const handleChange = (event) => {
    //setEdit(event.target.value);

    setEdit({...edit, tipe: event.target.value})
  };

  const date = new Date();
  const [selectedDate, setSelectedDate] = React.useState(date);


  const handleDateChange = (date) => {
    setSelectedDate(date);
    setEdit({...edit, tanggal: selectedDate });

  };

  const inputChangeHandler = (type) => (event) => {
    setEdit({...edit, [type]: event.target.value});
  };

  function editPosting(event){
    event.preventDefault();
    dispatch(actions.editPosting({...edit, idPosting: location.id}));
    history.push("/profile");
  }
	return(
		<Row>
		<Col>
		<Card>
			<CardBody>
				<Form  className="form-horizontal">
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Judul</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder={edit.judul} value={edit.judul} onChange={inputChangeHandler('judul')} />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Tanggal Terbit</Label>
						<Col sm="6">
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Grid container justify="space-around">
              <KeyboardDatePicker
                  disableToolbar
                  variant="inline"
                  format="dd/MM/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  label="Date picker inline"
                  value={edit.tanggal}
                  onChange={handleDateChange}
                  KeyboardButtonProps={{
                    'aria-label': 'change date',
                  }}
                />
              </Grid>
            </MuiPickersUtilsProvider>
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal"> Abstract </Label>
						<Col sm="6">
							<Input type="textarea" id="input-normal" name="input-large" placeholder={edit.abstrak} value={edit.abstrak} onChange={inputChangeHandler('abstrak')}  />
              <FormText color="muted">
                Apabila ingin mengupload pengabdian, abstrak tidak perlu diisi.
              </FormText>
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">Tipe</Label>
						<Col sm="6">
              <FormControl component="fieldset">
                <RadioGroup aria-label="gender" name="gender1" value={edit.tipe} onChange={handleChange}>
                  <FormControlLabel value="penelitian" control={<Radio />} label="Penelitian" />
                  <FormControlLabel value="pengabdian" control={<Radio />} label="Pengabdian" />
                </RadioGroup>
              </FormControl>
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">File</Label>
						<Col sm="6">
            <Input type="file" name="file" id="exampleFile" />
            <FormText color="muted">
              File harus berformat pdf.
            </FormText>
						</Col>
					</FormGroup>
            <Button variant="contained" color="primary" onClick={editPosting}>
            Submit
          </Button>

				</Form>
			</CardBody>
		</Card>
		</Col>
		</Row>

	);
}
