import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import 'bootstrap/dist/css/bootstrap.min.css';
import Login from './Login';
import MyRouter from "./MyRouter";
import {BrowserRouter} from 'react-router-dom';
import { Provider } from "react-redux";
import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import createSagaMiddleware from "redux-saga";

import memberReducer  from "./store/reducer/member";
import postingReducer  from "./store/reducer/posting";
import bukuReducer  from "./store/reducer/buku";
import hkiReducer from "./store/reducer/hki";
import akunReducer from "./store/reducer/akun";
import komenReducer from "./store/reducer/komen";
import { watchMember, watchPosting, watchBuku, watchHKI, watchAkun, watchKomen } from './store/sagas/index';

// ReactDOM.render(
  // <HashRouter>
  //   <MyRouter />
  // </HashRouter>,
//   document.getElementById('root')
// );

const rootReducer = combineReducers({
  member: memberReducer,
  posting: postingReducer,
  buku: bukuReducer,
  hki: hkiReducer,
  akun: akunReducer,
  komen: komenReducer
});

const sagaMiddleware = createSagaMiddleware();

const store = createStore(
  rootReducer,
  applyMiddleware(sagaMiddleware)
);

sagaMiddleware.run(watchMember);
sagaMiddleware.run(watchPosting);
sagaMiddleware.run(watchBuku);
sagaMiddleware.run(watchHKI);
sagaMiddleware.run(watchAkun);
sagaMiddleware.run(watchKomen);
const app = (
  <Provider store={store}>
  <BrowserRouter>
    <MyRouter />
  </BrowserRouter>,
  </Provider>
);

ReactDOM.render(app, document.getElementById("root"));

