export const lihatPostingSukses = (daftarPenelitian, daftarPengabdian) => {
  return{
    type: "POSTING_LIHAT_POSTING_SUKSES", //buat ke reducer
    daftarPenelitian, daftarPengabdian,
  }
}

export const lihatPosting = () => {
  return{
    type: "POSTING_LIHAT_POSTING",
  }
}

export const editPosting = (edit) => {
  return{
    type: "POSTING_EDIT_POSTING",
    edit,
  }
}

export const hapusPostingPenelitian = (idPosting) =>{
  return{
    type: "POSTING_HAPUS_POSTINGPENELITIAN",
    idPosting,
  }
}

export const hapusPostingPengabdian = (idPosting) =>{
  return{
    type: "POSTING_HAPUS_POSTINGPENGABDIAN",
    idPosting,
  }
}
