import { updateState } from "../utils/updateState";

const initialState = {
  daftarHKI: null,
  daftarHKIall: null,

};

const lihatHKISukses = (state, action) =>{
  return updateState(state, {daftarHKI: action.daftarHKI }  )
}

const lihatHKIallSukses = (state, action) =>{
  return updateState(state, {daftarHKIall: action.daftarHKIall }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "HKI_LIHAT_HKI_SUKSES":
    return lihatHKISukses(state, action);

    case "HKI_LIHAT_HKIALL_SUKSES":
    return lihatHKIallSukses(state, action);



    default: return state;

  }

};




export default reducer;
