import React, { useState, useEffect } from 'react';
import './App.css';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import Komen from './Komen';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import { useParams } from 'react-router';
import {config} from './config/firebase';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/posting';
import { useHistory } from 'react-router';
import * as actionsKomen from './store/actions/komen';


export default function Post() {

      const dispatch = useDispatch();

      const location = useParams();

      const daftarPenelitian = useSelector(state => state.posting.daftarPenelitian);
      useEffect( ()=> {
        dispatch(actions.lihatPosting());
      },[] )

      if(!daftarPenelitian) {
        return null;
      }


		return (
      <div className="animated fadeIn">
      <Penelitian name={daftarPenelitian[location.id].penulis} judul={daftarPenelitian[location.id].judul} tanggal={moment(daftarPenelitian[location.id].tanggal).format('LL')}/>
      <Abstract abstrak={daftarPenelitian[location.id].abstrak} />
      <br/><br/>
      <Komenn />
      </div>

	  );
}

function Abstract(props){
	return(
		<Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Abstract</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					{props.abstrak}
					</CardFooter>
				</Card>
			</Col>
		</Row>

	);
}

function Komenn(){
    const dispatch = useDispatch();
    const location = useParams();
    const {daftarKomen} = useSelector(state => state.komen);
    useEffect( ()=> {
      dispatch(actionsKomen.lihatKomen(location.id));
    },[] )

    if(!daftarKomen) {
      return null;
    }

  return(
    <div>
    {Object.keys(daftarKomen).map(key => (
      <Komen
          name={daftarKomen[key].penulis}
          komen={daftarKomen[key].komen}
        />
      ))}
    <InputKomen />
    </div>
  );
}

function InputKomen(){
  const history = useHistory();
  const dispatch = useDispatch();
  const member = useSelector(state => state.member);
  const location = useParams();

  const [edit, setEdit] = useState({
    komen: "",
    tanggal: new Date(),
    penulis: member.localId,
    idposting: location.id
  })


  const inputChangeHandler = (type) => (event) => {
    setEdit({...edit, [type]: event.target.value});
  };

  function tambahKomen(event){
    event.preventDefault();
    dispatch(actionsKomen.tambahKomen(edit));
    history.push("/post/"+location.id);
  }

  return(
    <div>
      <TextField id="standard-basic" label="Type here" placeholder={edit.komen} value={edit.komen} onChange={inputChangeHandler('komen')} />
      <Button variant="contained" color="primary" onClick={tambahKomen}>
        Komen
      </Button>
    </div>
  );
}
