export default function Follow() {
      return(
        <div className="animated fadeIn" >
          <Header />
          <DisabledTabs />
  			</div>
      );
}

function Header(){
  return(
    <div className="App judul">
      <h1> Follow with related researchers </h1>
      <p> Find and follow researchers to grow your network and keep up with their work.</p>
    </div>
  );
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
return {
  id: `full-width-tab-${index}`,
  'aria-controls': `full-width-tabpanel-${index}`,
};
}

const useStyles = makeStyles((theme) => ({
root: {
flexGrow: 1,
backgroundColor: theme.palette.background.paper,
},
}));

function DisabledTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
     setValue(index);
   };

  return (
    <div className={classes.root}>
     <AppBar position="static" color="default">
     <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant="fullWidth"
        aria-label="full width tabs example"
      >
         <Tab label="Summary" {...a11yProps(0)} />
         <Tab label="Instituation" {...a11yProps(1)} />
         <Tab label="Similar Interest" {...a11yProps(2)} />
       </Tabs>
     </AppBar>

     <SwipeableViews
       axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
       index={value}
       onChangeIndex={handleChangeIndex}
     >

     <TabPanel value={value} index={0} dir={theme.direction}>
       <Summary />
     </TabPanel>
     <TabPanel value={value} index={1} dir={theme.direction}>
       <Instituation />
     </TabPanel>
     <TabPanel value={value} index={2} dir={theme.direction}>
       <Similar />
     </TabPanel>
     </SwipeableViews>
   </div>
 );
}

const useStyless = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

function Summary(){

    const classes = useStyless();
    const dispatch = useDispatch();
    const {daftarAkun} = useSelector(state => state.akun);
    const localId = useSelector(state => state.member.localId);
    useEffect( ()=> {
      dispatch(actions.lihatAkun());
    },[] )

    if(!daftarAkun) {
      return null;
    }

  return(
    <div className={classes.root}>
      <br/>
        <Grid container spacing={3}>
        {Object.keys(daftarAkun).filter(key => {
          return key !== localId
          }).map(key => (
            <PreviewFollow
              name={daftarAkun[key].name}
              universitas={daftarAkun[key].universitas}
              jurusan={daftarAkun[key].jurusan}
              id={key}
            />
        ))}
        </Grid>
    </div>
  );
}


function Instituation(){

  const classes = useStyless();
  const dispatch = useDispatch();
  const {daftarAkun} = useSelector(state => state.akun);
  const localId = useSelector(state => state.member.localId);
  useEffect( ()=> {
    dispatch(actions.lihatAkun());
  },[] )

  if(!daftarAkun) {
    return null;
  }
  return(
    <div className={classes.root}>
      <br/>
        <Grid container spacing={3}>
          {Object.keys(daftarAkun).filter(key => {
            return key !== localId
            }).map(key => (
              <PreviewFollow
                name={daftarAkun[key].name}
                universitas={daftarAkun[key].universitas}
                jurusan={daftarAkun[key].jurusan}
                id={key}
                />
          ))}
        </Grid>
    </div>
  );
}

