import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/akun'

const API_KEY = config["apiKey"];

export function* lihatAkun(){
  const urlDBAkun       = `https://repository-1d687.firebaseio.com/user.json`;


try{
  const resDBBuku       = yield axios.get(urlDBAkun);



    yield put(actions.lihatAkunSukses(resDBBuku.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

}catch (error){
  console.log(error);

}

}
