import React, { Component, useState } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import {config} from './config/firebase';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/member';
import { useHistory } from 'react-router';

export default function Login() {
  const dispatch = useDispatch();
  const [login, setLogin] = useState({
    email: "",
    password: "",
  })


  const inputChangeHandler = (type) => (event) => {
    setLogin({...login, [type]: event.target.value});
  };

function submitLogin(event) {
  event.preventDefault();
  dispatch(actions.login(login.email, login.password))

}

const history = useHistory();
const idToken = localStorage.getItem("idToken");
    if(idToken) {
      history.push("/home");
    }

    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form onSubmit={submitLogin}>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="username" value={login.email} onChange={inputChangeHandler('email')}/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Password" autoComplete="current-password" value={login.password} onChange={inputChangeHandler('password')} />
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                        <Link to='/home'>
                          <Button color="primary" className="px-4" onClick={submitLogin} >Login</Button>
                          </Link>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Button color="link" className="px-0">Forgot password?</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
                <Card className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Selamat Datang di Apps. Silahkan login, apabila belum mempunyai akun silahkan klik tombol di bawah ini </p>
                      <Link to='/register'>
                        <Button color="primary" className="mt-3" active tabIndex={-1} >Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
}
