import { updateState } from "../utils/updateState";

const initialState = {
  daftarAkun: null,

};

const lihatAkunSukses = (state, action) =>{
  return updateState(state, {daftarAkun: action.daftarAkun }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "AKUN_LIHAT_AKUN_SUKSES":
    return lihatAkunSukses(state, action);



    default: return state;

  }

};

export default reducer;
