export default function Akun(){
    const location = useParams();
    const dispatch = useDispatch();
    const {daftarAkun} = useSelector(state => state.akun);

    useEffect( ()=> {
      dispatch(actionsAkun.lihatAkun());
    },[] )

    if(!daftarAkun) {
      return null;
    }



		return(
			<div className="animated fadeIn">
        <AccountCircleIcon style={{ fontSize: 80 }} />
  				<a className="App-nama"> {daftarAkun[location.id].name} </a>
          <Button variant="outlined" color="primary">
            Follow
          </Button>
  				<DisabledTabs  />
			</div>
		)
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
return {
  id: `full-width-tab-${index}`,
  'aria-controls': `full-width-tabpanel-${index}`,
};
}

const useStyles = makeStyles((theme) => ({
root: {
  flexGrow: 1,
  backgroundColor: theme.palette.background.paper,
},
}));


function DisabledTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
     setValue(index);
   };

  return (
    <div className={classes.root}>
     <AppBar position="static" color="default">
     <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant="fullWidth"
        aria-label="full width tabs example"
      >
         <Tab label="Overview" {...a11yProps(0)} />
         <Tab label="Post" {...a11yProps(1)} />
         <Tab label="Biodata" {...a11yProps(2)} />
         <Tab label="Penelitian" {...a11yProps(3)} />
         <Tab label="Pengabdian" {...a11yProps(4)} />
         <Tab label="Buku" {...a11yProps(5)} />
         <Tab label="HKI" {...a11yProps(6)} />
       </Tabs>
     </AppBar>

     <SwipeableViews
       axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
       index={value}
       onChangeIndex={handleChangeIndex}
     >

     <TabPanel value={value} index={0} dir={theme.direction}>
       <Overview />
     </TabPanel>
     <TabPanel value={value} index={1} dir={theme.direction}>
       <PostAkun />
     </TabPanel>
     <TabPanel value={value} index={2} dir={theme.direction}>
       <Biodata />
     </TabPanel>
     <TabPanel value={value} index={3} dir={theme.direction}>
       <Penelitiann />
     </TabPanel>
     <TabPanel value={value} index={4} dir={theme.direction}>
       <Pengabdiann />
     </TabPanel>
     <TabPanel value={value} index={5} dir={theme.direction}>
       <TabelBuku />
     </TabPanel>
     <TabPanel value={value} index={6} dir={theme.direction}>
       <TabelHKI />
     </TabPanel>
     </SwipeableViews>
   </div>
  );
}

function Overviewed(){
  return(
    <div>
      <Overview />
    </div>
  );
}

function PostAkun(){
  const location = useParams();
  const dispatch = useDispatch();
  const {daftarPengabdian, daftarPenelitian} = useSelector(state => state.posting);
  useEffect( ()=> {
    dispatch(actions.lihatPosting());
  },[] )

  if(!daftarPengabdian || !daftarPenelitian) {
    return null;
  }

  return (
    <div>
    {Object.keys(daftarPengabdian).filter(key => {
      return daftarPengabdian[key].id_penulis === location.id
    }).map(key => (
    <Pengabdian name={daftarPengabdian[key].penulis}
                judul={daftarPengabdian[key].judul}
                  tanggal={moment(daftarPengabdian[key].tanggal).format('LL')}
                  id={key}
                />
                ))}

      {Object.keys(daftarPenelitian).filter(key => {
        return daftarPenelitian[key].id_penulis === location.id
      }).map(key => (
      <Penelitian name={daftarPenelitian[key].penulis}
                  judul={daftarPenelitian[key].judul}
                    tanggal={moment(daftarPenelitian[key].tanggal).format('LL')}
                    id={key}
                  />
                  ))}
    </div>
  );
}

