import React, {Component} from 'react';
import '../App.css';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from '../store/actions/posting';

export default function PengabdianAkun(props){

  const dispatch = useDispatch();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickOpen = (idPosting) => {
    setOpen(idPosting);
  };

  const handleClosed = () => {
    setOpen(false);
  };

  function hapusPosting(event){

    dispatch(actions.hapusPostingPengabdian(open));
    setOpen(null);
  }
  return(
    <Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Pengabdian by {props.name}</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					<a className="App-judul">{props.judul}</a>
					<br/>
					<a> {props.tanggal}</a>
					</CardFooter>
				</Card>
        <Link to={"/postpengabdian/"+props.id}>
          <Button>
            <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a>
          </Button>
        </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
			</Col>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to={'/editPosting/' + props.id}>
        <MenuItem onClick={handleClose}>Edit</MenuItem>
        </Link>
        <MenuItem onClick={()=> handleClickOpen(props.id)}>Hapus</MenuItem>
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Hapus Posting?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Apakah anda yakin ingin menghapus postingan?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClosed} color="primary">
            Tidak
          </Button>
          <Button onClick={hapusPosting} color="primary" autoFocus>
            Ya
          </Button>
        </DialogActions>
      </Dialog>
      </Menu>
		</Row>
  );
}
