import React, { useEffect } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import Button from '@material-ui/core/Button';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import {config} from '../config/firebase';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from '../store/actions/hki';
import { useHistory } from 'react-router';

const API_KEY = config["apiKey"];
const databaseURL = config['databaseURL']

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});

export default function TabelHKI() {
  const classes = useStyles();

  const history = useHistory();
  const dispatch = useDispatch();
  const {daftarHKI} = useSelector(state => state.hki);
  const { name } = useSelector(({ member }) => member);

  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(null);

  useEffect( ()=> {
    dispatch(actions.lihatHKI());
  },[] )

  if(!daftarHKI) {
    return null;
  }

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickOpen = (idHKI) => {
    setOpen(idHKI);
  };

  const handleClosed = () => {
    setOpen(null);
  };

  function hapusHKI(event){

    dispatch(actions.hapusHKI(open));
    setOpen(null);
  }

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Judul</StyledTableCell>
            <StyledTableCell align="right">ID</StyledTableCell>
            <StyledTableCell align="right">Kategori</StyledTableCell>
            <StyledTableCell align="right">Tahun Permohonan</StyledTableCell>
            <StyledTableCell align="right">Inventor</StyledTableCell>
            <StyledTableCell align="right">Edit</StyledTableCell>
            <StyledTableCell align="right">Delete</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {Object.keys(daftarHKI).map(key => (
            <StyledTableRow key={daftarHKI[key].judul}>
              <StyledTableCell component="th" scope="row">
                {daftarHKI[key].judul}
              </StyledTableCell>
              <StyledTableCell align="right">{daftarHKI[key].id}</StyledTableCell>
              <StyledTableCell align="right">{daftarHKI[key].kategori}</StyledTableCell>
              <StyledTableCell align="right">{daftarHKI[key].tahun}</StyledTableCell>
              <StyledTableCell align="right">{name}</StyledTableCell>
              <StyledTableCell align="right"> <Link to={"/editHKI/"+key} > <IconButton> <EditIcon fontSize="small" /> </IconButton> </Link></StyledTableCell>
              <StyledTableCell align="right"><IconButton aria-label="delete" onClick={()=> handleClickOpen(key)}> <DeleteIcon fontSize="small"/> </IconButton> </StyledTableCell>
              <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Hapus Posting?"}</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  Apakah anda yakin ingin menghapus postingan?
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" onClick={handleClosed} color="primary">
                  Tidak
                </Button>
                <Button onClick={hapusHKI} color="primary" autoFocus>
                  Ya
                </Button>
              </DialogActions>
            </Dialog>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
