import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/member'

const API_KEY = config["apiKey"];

export function* register(action) {

  const {nik, alamat, bidang, universitas, jurusan, jenjang, name} = action.data;
  const signUp  = {...action.data,returnSecureToken: true}
  const url     = "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=" + API_KEY;
  const urlName =  "https://identitytoolkit.googleapis.com/v1/accounts:update?key=" + API_KEY;
  console.log("nama", name);


  try{
    const response = yield axios.post(url, signUp)
    const { idToken, refreshToken, localId, expiresIn, email } = response.data;

    const responseName = yield axios.post(urlName, {
      idToken: idToken,
      displayName: action.data.name,
    });

    const urlDB = `https://repository-1d687.firebaseio.com/user/${localId}.json`

    yield axios.put(urlDB, {nik, alamat, bidang, universitas, jurusan, jenjang, name})

    // nik: nik => nik

    // const name = responseName.data.displayName;

    const expirationDate = yield new Date(
      new Date().getTime() + expiresIn * 1000 );
    yield localStorage.setItem("idToken", idToken);
    yield localStorage.setItem("refreshToken", refreshToken);
    yield localStorage.setItem("expirationDate", expirationDate);
    yield localStorage.setItem("localId", localId);

    yield put(actions.registerSukses(idToken, localId, name, email));

  } catch (error){
    console.log(error);
  }

}

export function* login(action){
  const url = "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=" + API_KEY;
  const login = {email: action.email, password: action.password, returnSecureToken: true}

  try{

    const response = yield axios.post(url, login)

    const { email, expiresIn, idToken, localId, refreshToken} = response.data;

    const expirationDate = yield new Date(
      new Date().getTime() + expiresIn * 1000 );
    yield localStorage.setItem("idToken", idToken);
    yield localStorage.setItem("refreshToken", refreshToken);
    yield localStorage.setItem("expirationDate", expirationDate);
    yield localStorage.setItem("localId", localId);

    yield put(actions.loginSukses(idToken, localId, email));
  }catch(error){
    console.log(error);
  }
}

export function* authCheckState() {
  const idToken = localStorage.getItem("idToken");
  const localId = localStorage.getItem("localId");

  if (idToken) {
    const expirationDate = yield new Date(
      localStorage.getItem("expirationDate")
    );
    if (expirationDate > new Date()) {
      yield put(actions.getUserData(idToken, localId));
    } else if (expirationDate < new Date()) {
      const url = `https://securetoken.googleapis.com/v1/token?key=${API_KEY}`;
      const changeTokenData = {
        grant_type: "refresh_token",
        refresh_token: localStorage.getItem("refreshToken"),
      };

      try {
        const response = yield axios.post(url, changeTokenData);

        const { idToken, refreshToken, localId, expiresIn } = response.data;

        const expirationDate = yield new Date(
          new Date().getTime() + expiresIn * 1000
        );

        yield localStorage.setItem("idToken", idToken);
        yield localStorage.setItem("refreshToken", refreshToken);
        yield localStorage.setItem("localId", localId);
        yield localStorage.setItem("expirationDate", expirationDate);

        yield put(actions.getUserData(idToken, localId));
      } catch (error) {
        yield put(actions.logout());
      }
    } else {
      yield put(actions.logout());
    }
  } else {
    yield put(actions.logout());
  }
}

export function* getUserData(action) {
  const urlAuth = `https://identitytoolkit.googleapis.com/v1/accounts:lookup?key=${API_KEY}`;
  const urlDB = `https://repository-1d687.firebaseio.com/user/${action.localId}.json`;


  try {
    const resAuth = yield axios.post(urlAuth, {
      idToken: action.idToken,
    });
    const resDB = yield axios.get(urlDB);

    const {
      email,
      displayName,
      photoUrl,
    } = resAuth.data.users[0];

    const { alamat, bidang, jenjang, jurusan, nik, universitas} = resDB.data || {};

    yield put(
      actions.loginSukses(
        action.idToken,
        action.localId,
        email,
        nik,
        alamat,
        bidang,
        jenjang,
        jurusan,
        displayName,
        universitas,
      )
    );
  } catch (error) {
    console.log("ERROR", error)
    //yield put(actions.setResponse(error.response?.data));
  }
}


export function* logout() {
  try {
    yield call([localStorage, "clear"]);
  } catch (error) {
    console.log(error.response.data.error.message);
  }
}

export function* updateProfile(action) {
    let idToken = localStorage.getItem("idToken");
   const localId = localStorage.getItem("localId");

  const urlAuth = `https://identitytoolkit.googleapis.com/v1/accounts:lookup?key=${API_KEY}`;
  const urlDB = `https://repository-1d687.firebaseio.com/user/${localId}.json`;

  const editProfileData = {
     idToken: idToken,
     displayName: action.edit.name,
     email: action.edit.email,
     returnSecureToken: true,
    };

    const editProfileDataDB = {
        alamat: action.edit.alamat,
        bidang: action.edit.bidang,
        jenjang: action.edit.jenjang,
        jurusan: action.edit.jurusan,
        nik: action.edit.nik,
        universitas: action.edit.universitas,
        name: action.edit.name
    }

  try {
    const resAuth = yield axios.post(urlAuth, editProfileData);
    const resDB = yield axios.put(urlDB, editProfileDataDB);
    yield put(actions.getUserData(idToken, localId));
  } catch (error) {
    console.log("ERROR", error)
    //yield put(actions.setResponse(error.response?.data));
  }
}


export function* tambahPosting(action) {

  const {judul, tanggal, abstrak, file, tipe} = action.edit;

  const localId = localStorage.getItem("localId");
  try{

    const urlDB = `https://repository-1d687.firebaseio.com/${tipe}/.json`

    yield axios.post(urlDB, {judul, tanggal, abstrak, file, tipe, penulis: localId })


    // nik: nik => nik

  } catch (error){
    console.log(error);
  }

}
