import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/hki'

const API_KEY = config["apiKey"];

export function* tambahHKI(action) {

  const {judul, id, kategori, tahun} = action.edit;

  const localId = localStorage.getItem("localId");
  try{

    const urlDB = `https://repository-1d687.firebaseio.com/hki.json`

    yield axios.post(urlDB, {judul, id, kategori, tahun, penulis: localId })


    // nik: nik => nik

  } catch (error){
    console.log(error);
  }

}

export function* lihatHKI(){
  const localId         = localStorage.getItem("localId");
  const urlDBHKI         = `https://repository-1d687.firebaseio.com/hki.json?orderBy="penulis"&equalTo="${localId}"`;


try{
  const resDBHKI       = yield axios.get(urlDBHKI);

  yield put(actions.lihatHKISukses(resDBHKI.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

  }catch (error){
    console.log(error);

  }

}

export function* lihatHKIall(action){
  const urlDBHKI         = `https://repository-1d687.firebaseio.com/hki.json?orderBy="penulis"&equalTo="${action.id}"`;
  const urlDBUser       = 'https://repository-1d687.firebaseio.com/user.json';


try{
  const resDBHKI       = yield axios.get(urlDBHKI);
  const resDBUser       = yield axios.get(urlDBUser);

  for(let key in resDBHKI.data){
    resDBHKI.data[key].id_penulis = resDBHKI.data[key].penulis;
    resDBHKI.data[key].penulis = resDBUser.data[resDBHKI.data[key].penulis].name;
  }


  yield put(actions.lihatHKIallSukses(resDBHKI.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

  }catch (error){
    console.log(error);

  }

}

export function* editHKI(action){
  const localId         = localStorage.getItem("localId");

  const editDataHKI = {
    judul: action.edit.judul,
    id: action.edit.id,
    kategori: action.edit.kategori,
    tahun: action.edit.tahun,
    penulis: localId,
  }

  const urlDB = `https://repository-1d687.firebaseio.com/hki/${action.edit.idHKI}.json`;

  try{
    const resDB = yield axios.put(urlDB, editDataHKI);

  }catch(error){
    console.log(error);
  }
}

export function* hapusHKI(action){
  const urlDB = `https://repository-1d687.firebaseio.com/hki/${action.idHKI}.json`;

  try{
    const resDB = yield axios.delete(urlDB);
    yield put(actions.lihatHKI());
  }catch(error){
    console.log(error);
  }
}
