import React, { useState, useEffect } from 'react';
import './App.css';
import header from './header.JPG';
import logo from './Peteng2.png';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Snackbar from '@material-ui/core/Snackbar';
import CloseIcon from '@material-ui/icons/Close';
import axios from 'axios';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import {config} from './config/firebase';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/member';
import { useHistory } from 'react-router';

export default function EditProfile(){

		return(
			<div className="animated fadeIn">
        <Header />
        <Biodata  />

			</div>

		);
}

function Header(){
  return(
    <div className="App judul">
      <h1> Edit Profile </h1>
      <p> Benahi profile anda</p>
    </div>
  );
}

function Biodata(props) {
  const dispatch = useDispatch();
  const member = useSelector(state => state.member);
  const history = useHistory();

  const [edit, setEdit] = useState({
    email: "",
    name: "",
    nik: "",
    alamat: "",
    bidang: "",
    universitas: "",
    jurusan: "",
    jenjang: "",
    fotoProfile: ""
  })


  useEffect(() => {
    setEdit(member)
  }, [])

  const inputChangeHandler = (type) => (event) => {
    setEdit({...edit, [type]: event.target.value});
  };

  function updateData(event){
    event.preventDefault();
    dispatch(actions.updateProfile(edit));
    history.push("/profile");
  }

	return(
		<Row>
		<Col>
		<Card>
			<CardBody>
				<Form  className="form-horizontal">
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Nama</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder={edit.name} value={edit.name} onChange={inputChangeHandler('name')}  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">NIK</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-normal" placeholder={edit.nik} value={edit.nik} onChange={inputChangeHandler('nik')} />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Alamat</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-large" placeholder={edit.alamat} value={edit.alamat} onChange={inputChangeHandler('alamat')} />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Bidang</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="bidang" placeholder={edit.bidang} value={edit.bidang} onChange={inputChangeHandler('bidang')}  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Universitas</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="univ" placeholder={edit.universitas} value={edit.universitas} onChange={inputChangeHandler('universitas')}  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Jurusan</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="jurusan" placeholder={edit.jurusan} value={edit.jurusan} onChange={inputChangeHandler('jurusan')}  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Jenjang</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="jenjang" placeholder={edit.jenjang} value={edit.jenjang} onChange={inputChangeHandler('jenjang')}  />
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">Foto Profile</Label>
						<Col sm="6">
            <Input type="file" name="file" id="exampleFile" />
            <FormText color="muted">
              File harus berformat Jpeg.
            </FormText>
						</Col>
					</FormGroup>
          <Link to='/profile'>  <Button variant="contained" color="primary" onClick={updateData}>
              Submit Edit
            </Button>
            </Link>
				</Form>
			</CardBody>
		</Card>
		</Col>
		</Row>

	);
}
