import { takeEvery, all } from "redux-saga/effects";
import * as member from "./member";
import * as posting from "./posting";
import * as buku from "./buku";
import * as hki from "./hki";
import * as akun from "./akun";
import * as komen from "./komen";

export function* watchMember(){
  yield all([
    takeEvery("MEMBER_REGISTRASI", member.register),
    takeEvery("MEMBER_LOGIN", member.login),
    takeEvery("MEMBER_CHECK_STATE", member.authCheckState),
    takeEvery("MEMBER_GET_DATA",member.getUserData),
    takeEvery("MEMBER_LOGOUT", member.logout),
    takeEvery("MEMBER_UPDATE_PROFILE", member.updateProfile),
    takeEvery("MEMBER_TAMBAH_POSTING",member.tambahPosting),
  ])
}

export function* watchPosting(){
  yield all([
    takeEvery("POSTING_LIHAT_POSTING",posting.lihatPosting),
    takeEvery("POSTING_EDIT_POSTING",posting.editPosting),
    takeEvery("POSTING_HAPUS_POSTINGPENELITIAN",posting.hapusPostingPenelitian),
    takeEvery("POSTING_HAPUS_POSTINGPENGABDIAN",posting.hapusPostingPengabdian),
  ])
}

export function* watchBuku(){
  yield all([
    takeEvery("BUKU_TAMBAH_BUKU",buku.tambahBuku),
    takeEvery("BUKU_LIHAT_BUKU",buku.lihatBuku),
    takeEvery("BUKU_LIHAT_BUKUALL",buku.lihatBukuall),
    takeEvery("BUKU_EDIT_BUKU",buku.editBuku),
    takeEvery("BUKU_HAPUS_BUKU",buku.hapusBuku),
  ])
}

export function* watchHKI(){
  yield all([
    takeEvery("HKI_TAMBAH_HKI",hki.tambahHKI),
    takeEvery("HKI_LIHAT_HKI",hki.lihatHKI),
    takeEvery("HKI_LIHAT_HKIALL",hki.lihatHKIall),
    takeEvery("HKI_EDIT_HKI",hki.editHKI),
    takeEvery("HKI_HAPUS_HKI",hki.hapusHKI),
  ])
}

export function* watchAkun(){
  yield all([
    takeEvery("AKUN_LIHAT_AKUN",akun.lihatAkun),
  ])
}

export function* watchKomen(){
  yield all([
    takeEvery("KOMEN_TAMBAH_KOMEN",komen.tambahKomen),
    takeEvery("KOMEN_LIHAT_KOMEN",komen.lihatKomen),
  ])
}
