import React, {Component} from 'react';
import '../App.css';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';


const useStyless = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function PreviewFollow(props){

  const classes = useStyless();

  return(
    <Grid item xs={3}>
    <Card className={classes.root}>

       <CardContent>
        <Paper className={classes.paper}><AccountCircleIcon style={{ fontSize: 80 }} />
        <Button variant="outlined" color="primary">
          Follow
        </Button>
        <br/> <Link to= {'/akun/'+props.id}><a> {props.name} </a> </Link> <br/>
        <a>{props.universitas} </a><br/>
        <a> {props.jurusan} </a></Paper>
        </CardContent>

     </Card>
     </Grid>
  );
}
