import { config } from "../../config/firebase"
import axios from 'axios';
import { put, call } from 'redux-saga/effects';
import * as actions from '../actions/index';

export function* lihatPosting(){
  const urlDBPengabdian = `https://repository-1d687.firebaseio.com/pengabdian.json`;
  const urlDBPenelitian = `https://repository-1d687.firebaseio.com/penelitian.json`;
  const urlDBUser       = 'https://repository-1d687.firebaseio.com/user.json'

try{
  const resDBPengabdian = yield axios.get(urlDBPengabdian);
  const resDBPenelitian = yield axios.get(urlDBPenelitian);
  const resDBUser       = yield axios.get(urlDBUser);


  for(let key in resDBPengabdian.data){
    resDBPengabdian.data[key].id_penulis = resDBPengabdian.data[key].penulis;
    resDBPengabdian.data[key].penulis = resDBUser.data[resDBPengabdian.data[key].penulis].name;
  }

  for(let key in resDBPenelitian.data){
      resDBPenelitian.data[key].id_penulis = resDBPenelitian.data[key].penulis;
      resDBPenelitian.data[key].penulis = resDBUser.data[resDBPenelitian.data[key].penulis].name;
    }

    yield put(actions.lihatPostingSukses(resDBPenelitian.data, resDBPengabdian.data));

  // const resDBPenelitian = yield axios.get(urlDBPengabdian);
  // const { abstrak, file, judul, penulis, tipe } = resDBPenelitian.data || {};

}catch (error){
  console.log(error.response);

}

}

export function* editPosting(action){
  const localId   = localStorage.getItem("localId");

console.log('idposting', action.idPosting);
  const editDataPosting = {
    judul: action.edit.judul,
    tanggal: action.edit.tanggal,
    abstrak: action.edit.abstrak,
    file: action.edit.file,
    tipe: action.edit.tipe,
    penulis: localId,
  }

  const urlDB = `https://repository-1d687.firebaseio.com/${action.edit.tipe}/${action.edit.idPosting}.json`;



  try{
    const resDB = yield axios.put(urlDB, editDataPosting);

    console.log("balasan",resDB);
  }catch(error){
    console.log(error);
  }
}

export function* hapusPostingPenelitian(action){
  const urlDB = `https://repository-1d687.firebaseio.com/penelitian/${action.idPosting}.json`;

  try{
    const resDB = yield axios.delete(urlDB);
    yield put(actions.lihatPosting());
  }catch(error){
    console.log(error);
  }
}

export function* hapusPostingPengabdian(action){
  const urlDB = `https://repository-1d687.firebaseio.com/pengabdian/${action.idPosting}.json`;

  try{
    const resDB = yield axios.delete(urlDB);
    yield put(actions.lihatPosting());
  }catch(error){
    console.log(error);
  }
}
