import { updateState } from "../utils/updateState";

const initialState = {
  daftarKomen: null,

};

const lihatKomenSukses = (state, action) =>{
  return updateState(state, {daftarKomen: action.daftarKomen }  )
}

const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "KOMEN_LIHAT_KOMEN_SUKSES":
    return lihatKomenSukses(state, action);



    default: return state;

  }

};

export default reducer;
