import { updateState } from "../utils/updateState";

const initialState = {
  idToken: null,
  localId: null,
  email: null,
  nik : null,
  alamat : null,
  bidang: null,
  jenjang: null,
  jurusan: null,
  name: null,
  universitas: null,
};

const registrasiSukses = (state, actions) =>{
  return updateState(state, {localId:actions.localId, idToken:actions.idToken, email:actions.email, name:actions.name }  )
}

const loginSukses = (state, actions) =>{
  return updateState(state, {idToken:actions.idToken, localId:actions.localId,
    email:actions.email, nik:actions.nik, alamat:actions.alamat, bidang:actions.bidang,
     jenjang:actions.jenjang, jurusan:actions.jurusan, name:actions.name, universitas:actions.universitas  } )
}



const reducer = (state = initialState, action) =>{
  switch (action.type) {
    case "MEMBER_REGISTRASI_SUKSES":
    return registrasiSukses(state, action);

    case "MEMBER_LOGIN_SUKSES":
    return loginSukses(state, action);


    default: return state;

  }

};




export default reducer;
