import React, { useEffect } from 'react';
import './App.css';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import Komen from './Komen';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import { useParams } from 'react-router';
import {config} from './config/firebase';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/posting';


export default function PostPengabdian() {

      const dispatch = useDispatch();

      const location = useParams();

      const daftarPengabdian = useSelector(state => state.posting.daftarPengabdian);
      useEffect( ()=> {
        dispatch(actions.lihatPosting());
      },[] )

      if(!daftarPengabdian) {
        return null;
      }


		return (
      <div className="animated fadeIn">
      <Pengabdian name={daftarPengabdian[location.id].penulis} judul={daftarPengabdian[location.id].judul} tanggal={moment(daftarPengabdian[location.id].tanggal).format('LL')}/>
      <br/><br/>
      <Komen />
      </div>

	  );
}

function Abstract(props){
	return(
		<Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Abstract</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					{props.abstrak}
					</CardFooter>
				</Card>
			</Col>
		</Row>

	);
}
